import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';
import './About.css';



import worker from "../../images/handwerker.png";

class About extends Component {
  render() {
    return (
      <div className="aboutUs">
        <h1>ber uns</h1>

        <Row>

          <Col className="aboutStart" xs="12" sm="12" md="12" lg="12">
            <img src={worker} alt="worker" className="worker"/>
          </Col>

          <Col className="aboutStart" xs="12" sm="12" md="12" lg="12">
            <h2>Wer sind wir?</h2>
            <p className="aboutContent">
              Wir sind ein in zweiter Generation gefhrtes Familienunternehmen.
              Gegrndet wurde dieses 1980 von meinem Vater und ich habe dieses im Jahr 2000 bernommen.

              Damit unsere Dienstleistungen fachgerecht,
              sauber und in hchster Qualitt fr Sie durchgefhrt werden, setzen wir auf ein kleines,
              dafr bestens eingespieltes Team von Handwerkern und Fliesenlegern.
            </p>
          </Col>

          <Col className="aboutStart" xs="12" sm="12" md="12" lg="12">
            <h2>Was macht uns aus?</h2>
            <p className="aboutContent">
              Begeistert und fasziniert vom Fliesenhandwerk haben wir uns auf das Verlegen von Groformatfliesen spezialisiert.
              Denn groe Fliesen werden immer hufiger in groen Rumen und Bdern eingesetzt,
              damit die Rume grer und edler wirken.

              Da in unseren Augen Kreativitt im Fliesenhandwerk eine groe Rolle spielt,
              ist dieses lngst auch zu einer Kunst geworden. Daher koordinieren wir seit einigen Jahren auch komplette Badsanierungen
              und fhren die Planung mit Ihnen zusammen durch, sodass wir Sie umfassend von unseren kreativen Vorschlgen begeistern knnen.
            </p>
          </Col>

          <Col className="aboutStart" xs="12" sm="12" md="12" lg="12">
            <h2>Wieso sind wir Ihr Ansprechpartner?</h2>
            <p className="aboutContent">
              Kontinuierliche Weiterbildung und der Besuch auf europischen Messen zeigen uns stndig
              neue kreative Lsungen bei der Badplanung auf. So sind wir fr Sie immer auf dem
              aktuellsten Stand und knnen dies in Ihrem Projekt verwirklichen.

              Wir arbeiten Ihr Bauvorhaben nicht nach Schema F ab,
              sondern behandeln jedes Projekt individuell nach den rtlichen Gegebenheit
              und holen somit fr Sie das maximal beste Ergebnis heraus.

              Wenn Sie mchten, dass wir auch Ihr Projekt zu unserem machen,
              dann kontaktieren Sie uns doch einfach.
            </p>
          </Col>

        </Row>


      </div>
    );
  }
}

export default About;



